.movie-details-container {
    top: 0;
    color: white;
    position: absolute;
    box-sizing: border-box;
    left:0;
    right: 0;
    bottom: 0;
    z-index: 1000;
    min-height: 100%;
    background: #19181E;
    display: flex;
    flex-direction: column;
    padding: 3% 3%;
    transition: all linear .2s;
    height: auto;
}
.icon {
    font-size: 25px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    width: 40px;
    background: rgba(0, 0, 0, 0);
    box-shadow: 0px 0px 7px 0px rgba(256,256,256, 0.4);
    z-index: 10;
    border-radius: 50%;
    margin-bottom: 20px;
    opacity: .8;
}
.movie-overview-container {
    display: flex;
    flex-direction: row;
    gap: 4%;
    box-sizing: border-box;
    z-index: 10;
    overflow: hidden;
}
.image-container {
  width: 40%;
  max-width: 210px !important;
  height: 300px;
  margin: 2px;
  position: relative;
  background-position: center center;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 15px;
  box-shadow: 0px 0px 5px 0px rgba(256,256,256,.3);
}

/* .image-container img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    border-radius: 10px;
} */

.movie-floating-design {
    position: absolute;
    background: rgba(256,0,0, 0.6);
    top: 0;
    left: 0;
    right: 0;
    height: 150px;
    overflow: hidden;
}
.movie-floating-design img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    filter:blur(2px);
}

.movie-info-container {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-top: auto;
    margin-bottom: 2%;
}

.movie-title {
    font-size: 1.6rem;
    font-weight: 600;
}
.actor-sub-title {
    color: #FFD700;
    font-size: 1.1rem;
}
.rating-container {
    display: flex;
    flex-direction: column;
    gap: 1px;
}
.rating-value-container {
    display: flex;
    flex-direction: row;
    gap: 54px;
}
.rating-text-container {
    display: flex;
    flex-direction: row;
    gap: 30px;
}
.movie-genre-container {
    display: flex;
    flex-direction: row;
    gap: 20px;
}

.rating-star {
    color: yellow;
    font-size: 18px;
    margin-top: 2px;
}
.rating-value {
    color: yellow;
    font-weight: 700;
    font-size: 20px;
}
.genre-item {
    padding: 7px 17px;
    border-radius: 20px;
    background: gray;
}
.rating-text {
    opacity: .7;
    font-size: 18px;
}
.story-line-container {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    overflow: hidden;
}
.story-line-header {
    font-size: 1.6rem;
    font-weight: 600;
}
.story-line-content {
    text-align: justify;
    opacity: .8;
}

.actor-container {
    box-sizing: border-box;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    position: relative;
}

.actor-header {
    font-size: 1.6rem;
    font-weight: 600;
    overflow: hidden;
}

.actor-details-container {
    background: #19181E;
    display: grid;
    grid-auto-flow: column;
    grid-auto-columns: 205px;
    padding: 10px;
    overflow: scroll;
    gap: 10px;
    overflow-y: hidden;
    justify-items: start;
    transition: all linear .2s;

}

.actor-details-container::-webkit-scrollbar {
    height: 0;
    width: 0;
}

.actor-skeleton-container {
    overflow: hidden;
    box-sizing: border-box;
    height: 150px;
    min-width: 150px;
    background: #19181E;
    padding: 1%;
    display: flex;
    flex-direction: column;
    gap: 10%;
    animation: animateSkeleton 1s linear infinite;
}
@keyframes animateSkeleton {
    0% {

        opacity: 1;
    }
    50% {
        opacity: .5;
    }
    100% {
        opacity: 1;
    }
}
.actor-skeleton-image {
    height: 75%;
    width: 75%;
    border-radius: 50%;
    background: #595959;
    box-shadow: 0px 0px 4px 0px rgba(256, 256, 256, .2);   
}
.actor-skeleton-name {
    height: 10%;
    width: 57%;
    margin-left: 10px;
    background: #595959;
    box-shadow: 0px 0px 4px 0px rgba(256, 256, 256, .2);
}

.single-actor-details {
    display: flex;
    flex-direction: column;
    gap: 20px;
    box-sizing: border-box;
    background: inherit;
}

.actor-image-container {
    height: 120px;
    width: 120px;
    border-radius: 50%;
    overflow: hidden;
    background-position: center !important;
    background-size: cover !important;
    background-repeat: repeat !important;
    /* z-index: -1; */
    backdrop-filter: blur(10px);
    position: relative;
}
.actor-image-container::before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: 10;
}
.actor-image {
    height: 100%;
    width: 100%;
    object-fit: contain;
    position: relative;
    z-index: 11;
}

.actor-name {
    font-size: 1.2rem;
    opacity: .9;
}

/*actor details page css starts from here*/

.movie-info-container span {
    opacity: .8;
}

.dob-location-container {
    display: flex;
    flex-direction: row;
    gap: 10%;
}

.dob-container, .location-container {
    display: flex;
    gap: 10px;
    padding: 10px 17px;
    background: gray;
    border-radius: 20px;
}